.recharts-tooltip-wrapper {
  pointer-events: auto !important;
}

.info-detail {

  .table {
    .rc-table-cell {
      .liquidate {
        border: 1px solid #FFFFFF;
        padding: 4px 16px;
        border-radius: 16px;
        color: #ffffff;
      }

      .direction {
        border: 1px solid #93A1B8;
        color: #93A1B8;
        padding: 4px 16px;
        border-radius: 16px;
      }

      .Short,
      .RemoveLiquidity {
        border: 1px solid #CE5E5E;
        color: #CE5E5E;
        padding: 4px 16px;
        border-radius: 16px;
      }

      .Long,
      .AddLiquidity {
        border: 1px solid #59AE99;
        color: #59AE99;
        padding: 4px 16px;
        border-radius: 16px;
      }
    }
  }
}

.info-detail {
  .title {
    display: flex;
  }

  .tab-mining {
    display: flex;
    align-items: center;
    font-weight: bold;
    margin-left: 96px;

    a:hover {
      text-decoration: none;
    }

    .pool-active,
    .pool-unactive {
      box-sizing: content-box;
      padding: 24px 24px;
      line-height: 0;
      font-size: 14px;
      cursor: pointer;
    }


    .pool-active {
      color: #FFFFFF;
      background: #203B60;
      border-radius: 4px;
    }
  }

  .tip-cursor {
    width: 100px;
    height: 40px;
    border: 1px solid #203B60;
    background-color: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 999;
  }

  // .recharts-legend-wrapper {
  //   top: 0;
  //   height: 0 !important;
  // }

  .token-list {
    position: absolute;
    right: 0;
    // display: flex;
    // width: 100%;
    height: 118px;
    flex-wrap: wrap;
    padding-left: 24px;
    padding-right: 24px;

    // justify-content: flex-start;
    li {
      list-style: none;
      font-size: 20px;
      display: flex;
      width: 160px;
      justify-content: space-between;
      margin-top: 8px;
      margin-left: 10px;

      .WBNB,
      .BNB,
      .WBTC,
      .WMATIC,
      .amUSDC,
      .BUSD,
      .USDT,
      .DERI,
      .TBUSD,
      .SXP,
      .WBNB,
      .ETH,
      .LTC,
      .XRP,
      .BTCB,
      .ADA,
      .Cake,
      .MATIC,
      .AAVE,
      .TUSD,
      .DAI,
      .DOT,
      .LINK,
      .XVS,
      .USDC,
      .TRX,
      .TDERI,
      .UST,
      .ARB,
      .DOGE,
      .MANTA,
      .wstETH,
      .LUNA,
      .USDB {
        width: 20px;
        height: 20px;
        margin-right: 8px;
        margin-left: 8px;
        background-size: 20px 20px !important;
        background-repeat: no-repeat;
        background-position: center;
      }

      .USDB {
        background-image: url("../../assets/img/USDB.svg");
      }

      .wstETH {
        background-image: url("../../assets/img/WSTETH.svg");
      }

      .MANTA {
        background-image: url('../../assets/img/MANTA.svg');
      }

      .ARB {
        background-image: url('../../assets/img/ARB.svg');
      }

      .LTC {
        background-image: url('../../assets/img/LTC.svg');
      }

      .DOGE {
        background-image: url('../../assets/img/DOGE.svg');
      }

      .UST {
        background-image: url('../../assets/img/UST.svg');
      }

      .LUNA {
        background-image: url('../../assets/img/LUNA.svg');
      }

      .XRP {
        background-image: url('../../assets/img/XRP.svg');
      }

      .XVS {
        background-image: url('../../assets/img/XRP.svg');
      }

      .DAI {
        background-image: url('../../assets/img/DAI.svg');
      }

      .DOT {
        background-image: url('../../assets/img/DOT.svg');
      }

      .LINK {
        background-image: url('../../assets/img/LINK.svg');
      }


      .ADA {
        background-image: url('../../assets/img/ADA.svg');
      }

      .AAVE {
        background-image: url('../../assets/img/AAVE.svg');
      }

      .TUSD {
        background-image: url('../../assets/img/TUSD.svg');
      }

      .TRX {
        background-image: url('../../assets/img/TRX.svg');
      }

      .SXP {
        background-image: url('../../assets/img/SXP.svg');
      }

      .USDC {
        background-image: url('../../assets/img/USDC.svg');
      }

      .DERI,
      .TDERI {
        background-image: url('../../assets/img/logo.svg');
      }

      .ETH {
        background-image: url('../../assets/img/ETH.svg');
      }

      .BTCB,
      .WBTC {
        background-image: url('../../assets/img/BTC.svg');
      }

      .WBNB,
      .BNB {
        background-image: url('../../assets/img/BNB.svg');
      }

      .WMATIC,
      .MATIC {
        background-image: url('../../assets/img/MATIC.svg');
      }

      .amUSDC {
        background-image: url('../../assets/img/amusdc.svg');
      }

      .BUSD,
      .TBUSD {
        background-image: url('../../assets/img/BUSD.svg');
      }

      .USDT {
        background-image: url('../../assets/img/USDT.svg');
      }

      .Cake {
        background-image: url('../../assets/img/Cake.svg');
      }

      span {
        display: flex;

        img {
          margin: 0 8px;
        }
      }

      .token-name {
        width: 102px;
      }
    }
  }
}

.tokens {
  .tokens-filter {
    margin-bottom: 50px;
  }
}

@media only screen and (min-width:500px) {
  .info-detail {
    .recharts-legend-wrapper {
      top: 0;
      height: 0 !important;
    }
  }
}




@media only screen and (max-width:760px) {
  .info-detail {
    .tab-mining {
      margin-left: 36px;

      .pool-active,
      .pool-unactive {
        font-size: 12px;
      }
    }
  }

  .info-main {
    .list-by-chain {
      .table {
        .rc-table-cell:first-child {
          padding-left: 12px;
        }

        .rc-table-cell {
          min-width: 150px;
          text-align: left;
        }
      }
    }

    .charts {
      .graph {
        min-width: 355px;
      }

      .netValue,
      .tokens {
        .filter {
          .g-button {
            width: 64px;
          }
        }
      }

      .tokens {

        height: 620px;

        .token-list {
          display: flex;
          justify-content: center;
        }
      }
    }
  }
}